import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Params, Router } from '@angular/router';
import {
  LoadingController,
  ModalController,
  NavController,
  ToastController,
} from '@ionic/angular';
import { plainToClass } from 'class-transformer';
import { BasePage } from 'src/app/base/base-page';
import { ArticleBean } from 'src/app/beans/article-bean';
import { PagerBean } from 'src/app/beans/pager-bean';
import { ResponseBean } from 'src/app/beans/response-bean';
import { StatusContentComponent } from 'src/app/components/status-content/status-content.component';
import { ScreenFitService } from 'src/app/providers/screen-fit.service';
import { ThemeService } from 'src/app/providers/theme.service';
import { ApiService } from 'src/app/services/api.service';
import { RuntimeService } from 'src/app/services/runtime.service';

@Component({
  selector: 'app-search-result',
  templateUrl: './search-result.page.html',
  styleUrls: ['./search-result.page.scss'],
})
export class SearchResultPage extends BasePage implements OnInit {
  @ViewChild('appStatusContent', { static: false })
  appStatusContent: StatusContentComponent;

  searchKeyword: string = '';
  articleList: ArticleBean[] = [];
  pageIndex: number = 1;
  pageSize: number = 20;
  resultTotal: number = 0;

  constructor(
    protected loadingCtrl: LoadingController,
    protected toastCtrl: ToastController,
    protected runtime: RuntimeService,
    protected screenFitService: ScreenFitService,
    private navCtrl: NavController,
    private modalCtrl: ModalController,
    private api: ApiService,
    private router: Router,
    private routeInfo: ActivatedRoute,
    private themeService: ThemeService
  ) {
    super(loadingCtrl, toastCtrl, runtime, screenFitService);
    this.routeInfo.queryParams.subscribe((params: Params) => {
      this.searchKeyword = decodeURIComponent(params.keyword);
    });
  }

  ngOnInit() {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        if (event.url == '/search-result') {
          this.setPageTitle('SearchResult');
        }
      }
    });
  }

  async initData() {
    // fetch data or load cache
    await Promise.all([this.fetchArticleList()]);

    this.appStatusContent.invokeSuccess();
  }

  ionViewWillEnter() {
    this.setPageTitle('SearchResult');
  }

  ionViewDidEnter() {
    (async () => {
      await this.initData();
    })().catch((error) => {
      console.error('[init data errror]', error);
      this.appStatusContent.invokeError();
    });
  }

  onRetry() {
    (async () => {
      await this.initData();
    })().catch((error) => {
      console.error('[init data errror]', error);
      this.appStatusContent.invokeError();
    });
  }

  back() {
    this.navCtrl.back();
  }

  onClickDeleteKeyword() {
    this.searchKeyword = '';
  }

  onKeyup(event: KeyboardEvent) {
    switch (event.code) {
      case 'Enter':
        if (this.searchKeyword) {
          this.router.navigate(['/search-result'], {
            queryParams: {
              keyword: encodeURIComponent(this.searchKeyword),
            },
          });
        } else {
          this.showToast('请输入搜索内容');
        }
        break;
    }
  }

  onClickArticleItem(item: ArticleBean) {
    this.router.navigate(['/article-detail'], {
      queryParams: {
        uid: item.uid,
      },
    });
  }

  async fetchArticleList() {
    const response: ResponseBean<PagerBean<ArticleBean>> = plainToClass(
      ResponseBean,
      await this.api.fetchArticleList(null)
    );
    if (response.code == 200) {
      this.resultTotal = response.result.total;

      if (response.result.data && response.result.data.length > 0) {
        this.articleList = response.result.data;
      }
    } else {
      return Promise.reject({ code: response.code, message: response.message });
    }
  }
}
